import { Card, Image } from 'semantic-ui-react';
import {REACT_APP_API_STATIC} from '../utils/consts';

let AppItem = ({info})=>{
    let {app_name, app_id} = info;
    return(
        <Card style={{'width' : '150px', margin : '0 auto'}} link={true}>
            <Image src={`${REACT_APP_API_STATIC}app/${app_id}/logo.png`} wrapped ui={false}/>
            <Card.Content textAlign={'center'}>
                <Card.Header style={{height : '23.15px', overflow : 'hidden', textOverflow : 'ellipsis',  whiteSpace: 'nowrap'}}>{app_name}</Card.Header>
            </Card.Content>
        </Card>
    );
};

export default AppItem;