import { Routes ,Route } from 'react-router-dom';
import { publicRoutes } from '../routes';

let AppRouter = ()=>{
    return(
        <Routes>
            {publicRoutes.map(({path, Component}, index)=>{
                return <Route key={index} path={path} element={<Component/>} exact/>
            })}
        </Routes>
    )
};

export default AppRouter;