import { useState, useContext, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import { Context } from '../../index';
import { Image, List, Header, Grid, Segment, Button, Container, Icon, Dropdown, Message } from 'semantic-ui-react';
import { REACT_APP_API_STATIC } from '../../utils/consts';
import AppLoading from '../../components/AppLoading';
import './AppPage.css';

let UnSupport = ()=>{
    return (
        <>
            <p>
            Твой браузер не поддерживает установку pwa приложений. Чтобы установить хоть что-то, начни пользоваться одним из браузеров ниже.
            </p>
            <List style={{marginTop : '15px'}}>
                <List.Item targer="_blank" as='a' href="https://www.google.com/chrome/">
                    Google Chrome
                </List.Item>
                <List.Item targer="_blank" as='a' href="https://www.microsoft.com/edge">
                    Microsoft Edge
                </List.Item>
            </List>
        </>
    );
}

let AppPage = ()=>{
    let {user, apps} = useContext(Context);
    let {id} = useParams();
    let [appInfo, setAppInfo] = useState();
    let [pageLoading, setPageLoading] = useState(true);
    let [installLoading, setInstallLoading] = useState(false);
    let [pageWidth, setPageWidth] = useState(1000);
    let [message, setMessage] = useState({}); 
    
    useEffect(()=>{
        let query = new URLSearchParams(window.location.search), status = query.get('status'), localApp;
        apps._appList.some(item =>{
            if(item.app_id === id){
                setAppInfo(item);
                localApp = item;
                return true;
            }
        });
        if(localApp){
            if(status == "'redirect'" && user._appInstalled) window.location.replace(localApp.app_link);
            else {
                window.addEventListener('resize',()=>{
                    setPageWidth(window.innerWidth);
                });
                
                if(!user._appInstallSupport) {
                    messageHandler('info','Нет поддержки!', <UnSupport/>)
                }
                setTimeout(()=>{setPageLoading(false)},[998]);
            }
        }
    },[]);

    let messageHandler = (type, header, message)=>{
        setMessage({type, header, message});
    }

    let installHendler = ()=>{
        try{
            setInstallLoading(true);
            user.regManifest(`../${REACT_APP_API_STATIC}app/${appInfo.app_id}/site.webmanifest`);
            setTimeout(async ()=>{
                if(typeof user._appEvent.prompt === 'function'){
                    user._appEvent.prompt();
                    let { outcome } = await user._appEvent.userChoice;
                    if(outcome === 'accepted')
                        window.location.replace(`/app/${appInfo.app_id}?status='redirect'`);
                } else messageHandler('negative','Произошла ошибка!','При установке произошла ошибка, повтори попытку через некоторое время.');
                setInstallLoading(false);
            },[999]);
        } catch (e) {
            messageHandler('negative','Произошла ошибка!','При установке произошла ошибка, повтори попытку через некоторое время.');
        }
    }

    let wisitSite = ()=>{
        return window.open(appInfo.app_link, '_blank');
    }
    
    return (
        <Segment className="appPageSegment">
            {pageLoading && <AppLoading/>}
            {!pageLoading &&
                <>
                    <Grid columns={2}>
                        <Grid.Row>
                            <Grid.Column mobile={16} tablet={10} computer={10}>
                                <Image width={pageWidth < 767 ? "458px" : "303px"} src={`../${REACT_APP_API_STATIC}app/${appInfo.app_id}/logo.png`}/>
                            </Grid.Column>
                            <Grid.Column mobile={16} tablet={6} computer={6}>
                                <Container className={pageWidth < 767 ? 'mobile-header' : 'none'}>
                                    <Header as='h2'>{appInfo.app_name}</Header>
                                    <p>{appInfo.app_description}</p>
                                    <Dropdown as="a" text='Инфо' className="dropMenu">
                                        <Dropdown.Menu>
                                            <Dropdown.Item onClick={wisitSite} icon="external alternate" text='Перейти на сайт приложения' />
                                        </Dropdown.Menu>
                                    </Dropdown>
                                    <Button disabled={'header' in message || installLoading || !user._appInstallSupport} className="installButton" onClick={installHendler} loading={installLoading} primary><Icon name='arrow alternate circle down outline'/>Установить</Button>
                                </Container>
                            </Grid.Column>
                        </Grid.Row>
                    </Grid>
                    {message.header &&
                    
                        <Message negative={message.type === 'negative'} className="messageBlock">
                            <Message.Header>{message.header}</Message.Header>
                            {message.type === 'negative' &&
                                <p>{message.message}</p>
                            }
                            {message.type === 'info' &&
                                <>{message.message}</>
                            }
                        </Message>
                    
                    }
                </>
            }
        </Segment>
    )
}

export default AppPage;