import { useEffect, useState, useContext } from 'react';
import { Grid } from 'semantic-ui-react';
import { observer } from 'mobx-react-lite';
import { useNavigate } from 'react-router-dom';
import { Context } from '../index';
import AppItem from '../components/AppItem';

let Main = observer(()=>{
    let [appList, setAppList] = useState([]);
    let { apps } = useContext(Context);
    let navigate = useNavigate();

    useEffect(()=>{
        apps._appList.sort((a, b)=>{
            let nameA = a.app_name.toLowerCase(), nameB = b.app_name.toLowerCase();
            if(nameA<nameB) return -1;
            else if(nameA>nameB) return 1;
        });
        setAppList(apps._appList);
       
    },[]);

    return(
        <Grid centered style={{width : '100%', margin : '20px auto', maxWidth : '750px'}}>
            {appList.map((data, index)=>{
                return <Grid.Column mobile={10} tablet={5} computer={4} key={index} onClick={()=>{navigate(`/app/${data.app_id}`)}}>
                    <AppItem info={data}/>
                </Grid.Column>
            })}
        </Grid>
    );
});

export default Main;