import { useEffect, useState } from 'react';
import { Icon } from 'semantic-ui-react';

let Footer = ()=>{
    let [windowWidth, setWindowWidth] = useState(window.innerWidth);
    useEffect(()=>{
        window.addEventListener('resize', ()=>{
            setWindowWidth(window.innerWidth)
        });
    },[]);
    return(
        <div style={{margin:'45px auto', maxWidth : '750px','width' : '100%', padding : '15px'}}>
            <div style={{
                "display": "inline-block"
            }}>
                <a href="https://t.me/codemvp" style={{"marginRight" : "10px"}}><Icon name='telegram' size='large'/></a>
                <a href="mailto:anton.burskii@gmail.com">
                    {windowWidth>500 && 
                        <>anton.burskii@gmail.com</>
                    }
                    {windowWidth<500 && 
                        <Icon name='mail' size='large'/>
                    }
                </a>
            </div>
            <div style={{
                "display": "inline-block",
                "float" : "right"
            }}>
                 pwabox © {new Date().getFullYear()}
            </div>
      </div>
    )
};

export default Footer;