import { BrowserRouter as Router } from "react-router-dom";
import { useEffect, useContext, useState } from "react";
import { Context } from './index';
import { osName, browserName } from 'react-device-detect';
import AppRouter from "./components/AppRouter";
import NavBar from "./components/NavBar";
import Footer from "./components/Footer";
import AppLoading from "./components/AppLoading";
import "./App.css";

let App = ()=> {

  let {user} = useContext(Context);
  let [loading, setLoading] = useState(true);

  useEffect(()=>{
    let acceptList = ['Windows/Chrome','Windows/Edge', 'Mac OS/Chrome', 'Mac OS/Edge', 'iOS/Mobile Safari', 'Android/Chrome', 'Android/Samsung Browser'];
    acceptList.forEach((item)=>{
      if(item === osName+'/'+browserName) return user.setSupport(true);
    });
    user.setStatus(window.matchMedia('(display-mode: standalone)').matches);
    setTimeout(()=>{setLoading(false);}, 999);
  },[]);
  
  return (
    <Router>
      {loading && <AppLoading/>}
      {!loading && 
        <>
          <NavBar/>
          <AppRouter/>
          <Footer/>
        </>
      }
    </Router>
  );
}

export default App;
